
import XHeader from 'vux/src/components/x-header/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import Scroll from 'assembly/scroll/scroll'
import localStore from 'util/localStore'
import { mapActions } from 'vuex'

export default {
components: {
XHeader,
Confirm,
Scroll,
Popup
},
data () {
return {
fmsClientOrderVO: {},
orderNo: '',
status: 0,
products: [],
promotions: [],
clientGifts: [],
ifNotZeng: false, // 赠品不足弹窗
NotProductList: [], // 不足的主产品或换赠品列表
totalNum: 0,
inited: false, // 防止狂点
balance: 0,
countDownTime: 0, // 赠品不足倒计时时间（单位：秒）
timer: null 
}
},
methods: {
goBack () {
this.$router.push({
path: '/order'
})
},
formatePrice (num) {
return num / 100
},
countDown (time) {
let m, s
m = '0' + Math.floor(time / 60)
s = Math.floor(time - m * 60)
this.timer = setInterval(() => {
s--
s < 10 && (s = '0' + s)
if (s.length >= 3) {
s = 59
m = '0' + (Number(m) - 1)
}
if (m.length >= 3) {
m = '00'
s = '00'
clearInterval(this.timer)
this.$vux.toast.text('下单超时，系统自动取消订单~')
this.$router.go(-1)
}
this.countDownTime = m + '分' + s + '秒'
}, 1000)
},
cancelOrder () {
if (this.inited) {
return 
}
this.inited = true
this.$tupHttp(this, 'front/shoppingCart/cancelTemporaryOrder?orderNo=' + this.orderNo, 'POST').then(res => {
this.$vux.toast.text('取消成功')
clearInterval(this.timer)
this.ifNotZeng = false
this.$router.push({
path: '/shoppingCart'
})
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
confirmToPay () {
if (this.inited) {
return 
}
this.inited = true
this.$tupHttp(this, 'front/shoppingCart/confirmTemporaryOrder?orderNo=' + this.orderNo, 'POST').then(res => {
this.clearCart() 
this.$vux.toast.text('订单已提交，请支付')
localStore.set('TUP_pFirstOrderFlag', true) 
clearInterval(this.timer)
this.ifNotZeng = false
this._getClientGiftProducts()
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_getClientGiftProducts () {
this.$tupHttp(this, 'front/shoppingCart/getClientGiftProducts').then(rs => { // 获取指定赠送
localStore.set('TUP_cart', {
'promotion': [],
'product': [],
'giftProducts': rs.data.models || [],
'needGiftProduct': 1
})
this._selectMustPromotionsOrProducts() 
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_selectMustPromotionsOrProducts () { // 获取必订
this.$tupHttp(this, 'front/product/selectMustPromotionsOrProducts').then(res => {
let data = res.data.model
if (data) {
let cart = localStore.get('TUP_cart')
data.promotionItemVOS = data.promotionItemVOS.map(item => {
item.promotionRuleVO.productList.map(childItem => {
if (childItem.isHave) {
childItem.selected = true
}
if (childItem.mustQty) {
childItem.selected = true
childItem.shopCartNum = childItem.mustQty
}
return childItem
})
item.shopCartNum = 1
item.ifEdit = false
item.keyProductPrice = 0
item.promotionTotalPrice = 0
return item
})
cart.promotion = cart.promotion.concat(data.promotionItemVOS)
if (localStore.get('TUP_pFirstorderDate') !== null) { // 首订用户不允许加入常规产品
data.solrProductVos = data.solrProductVos.map(item => {
item.shopCartNum = item.mustQty
item.selected = true
return item
})
cart.product = cart.product.concat(data.solrProductVos)
}
this.saveCart({
type: 3,
item: {
product: cart.product,
promotion: cart.promotion
},
isManual: 0
})
}
this._getFullDiscount() 
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_getFullDiscount () {
this.$tupHttp(this, 'front/product/selectPromotionsByClientIdFromSolr?subType=0').then(res => {
let data = res.data.models
for (let i = 0, len = data.length; i < len; i++) {
this.setPromotionCart({
item: data[i],
isManual: 0
})
}
this.$router.replace({
path: '/orderDetail',
query: {
orderNo: this.orderNo
}
})
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_getDetail () {
this.$tupHttp(this, 'front/order/getClientOrderDetail/' + this.orderNo).then(res => {
this.fmsClientOrderVO = res.data.model.fmsClientOrderVO
this.products = res.data.model.products
this.promotions = res.data.model.promotions
this.clientGifts = res.data.model.clientGifts

for (let i = 0; i < this.promotions.length; i++) {
this.totalNum += this.promotions[i].productList.length + this.promotions[i].giftList.length
}
this.totalNum += this.products.length
this.$tupHttp(this, 'front/shoppingCart/getTemporaryOrder').then(res => {
if (!res.data.extra) {
this.$vux.toast.text('订单已被超时取消~')
this.$router.push({
path: '/shoppingCart'
})
return
}
this.NotProductList = res.data.models.filter(item => { // 筛选下单不成功的产品
return item.bizErrorMsg
})
this.ifNotZeng = true
this.countDown(res.data.extra.countDownTime)
})
})
},
...mapActions([
'saveCart',
'setPromotionCart',
'clearCart'
])
},
created () {
this.orderNo = this.$route.query.orderNo
if (this.orderNo) {
this._getDetail()
}
}
}



